import React from "react";
import Modal from 'components/Modal'
import moment from 'moment'
import { Link, IndexLink } from 'react-router'
import swal from "sweetalert2"
import cookie from 'js-cookie';
export default class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: null,
    }
  }
  componentDidMount() {

  }
  componentWillReceiveProps(nextProps) {
   
  }
  handleClosePopup() {
    this.setState({ modal: null })
    $('body').removeClass('modal-open');
  }
  handleOpenPopup(modal) {
    this.setState({ modal: modal })
    $('body').addClass('modal-open');
  }
  handleLogOut(){
    cookie.set('userQR', null);
    cookie.set('csrftoken', null);
    window.location.href="/dang-nhap";
  }
  render() {
    let { currentUser } = this.props.currentUser;
    const { modal } = this.state
    return (
      <React.Fragment>
        <header>
          <nav className="fo4-container">
            <div className="collapse" id="navbarSupportedContent">
              <ul className="menu">
                <li className="nav-item logo">
                  <a className="nav-link" href="javascript:void(0)"><span className="light-bold"> actech<b>plus</b></span></a>
                </li>
                <li className="menu-sub">
                  <ul>
                    <li>
                      <a href="/">l tem</a>
                    </li>
                    <li>
                      <a href="/nguoi-dung">ngi dng</a>
                    </li>
                    <li>
                      <a href="/thong-ke">thng k</a>
                    </li>
                  </ul>
                </li>

                {!currentUser ? (
                  <React.Fragment>
                    <li>
                      <a href="/dang-nhap"><span>NG NHP</span></a>
                    </li>
                  </React.Fragment>
                ) : (
                    <React.Fragment>
                      <li>
                        <a href="javascript:void(0)" onClick={e => this.handleLogOut()} title="ng Xut" className="txt-user" title="ng xut">
                          <span className="username-txt">xin cho, {currentUser.userName}  </span>
                          <span className="logout-text">&nbsp;|&nbsp;ng Xut</span>
                        </a>
                      </li>
                    </React.Fragment>
                  )}

              </ul>
            </div>
          </nav>
        </header>
      </React.Fragment>
    );
  }
}



