export function showErrorWithCode(errCode) {
  let errorMess
  switch (errCode) {
    case 'Incorrect username or password':
      errorMess = 'Tn ng nhp hoc ti khon khng ng.'
      break;

    default:
      errorMess = errCode;
      break;
  }
  return errorMess
}


export function smoothScroll(target) {
  if (!target) {
    return
  }
  $('html,body').stop().animate({
    scrollTop: $(target).offset().top
  }, 1000);
}
